import React, { useState, useEffect, useMemo, useRef } from 'react';
import { ChevronRight, Search, BarChart2, FileText, Download, X } from 'lucide-react';

const Tooltip = ({ children, text }) => (
  <div className="group relative flex items-center">
    {children}
    <span className="absolute bottom-full right-0 bg-gray-800 text-white text-xs rounded py-1 px-2 opacity-0 group-hover:opacity-100 transition-opacity duration-300 whitespace-nowrap">
      {text}
    </span>
  </div>
);

const Sidebar = ({ isOpen, onClose, articles, title, breadcrumb }) => {
  const [width, setWidth] = useState(320); // Set initial width

  const sidebarRef = useRef(null);
  const resizerRef = useRef(null);
  const [isResizing, setIsResizing] = useState(false);

  const handleMouseDown = (e) => {
    setIsResizing(true);
    e.preventDefault();
  };

  const handleMouseMove = (e) => {
    if (!isResizing) return;
    const newWidth = window.innerWidth - e.clientX;
    if (newWidth >= 200) {
      setWidth(newWidth);
    }
  };

  const handleMouseUp = () => {
    setIsResizing(false);
  };

  useEffect(() => {
    if (isResizing) {
      document.addEventListener('mousemove', handleMouseMove);
      document.addEventListener('mouseup', handleMouseUp);
    } else {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    }
    return () => {
      document.removeEventListener('mousemove', handleMouseMove);
      document.removeEventListener('mouseup', handleMouseUp);
    };
  }, [isResizing]);

  return (
    <div
      ref={sidebarRef}
      style={{ width }}
      className={`fixed top-0 right-0 h-full bg-white shadow-lg transform transition-transform duration-300 ease-in-out z-50 overflow-y-auto ${isOpen ? 'translate-x-0' : 'translate-x-full'}`}
    >
      <div className="p-6 border-b border-indigo-200">
        <div className="flex items-center text-sm text-gray-600 mb-4">
          <span className="mr-2">Path:</span>
          {breadcrumb.slice(1).map((item, index) => (
            <React.Fragment key={index}>
              <span className="font-medium text-indigo-600">{item}</span>
              {index < breadcrumb.length - 2 && (
                <ChevronRight size={16} className="mx-2 text-gray-400" />
              )}
            </React.Fragment>
          ))}
        </div>
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold text-indigo-700">Related Articles to {title}</h2>
          <button onClick={onClose} className="text-gray-500 hover:text-gray-700">
            <X size={24} />
          </button>
        </div>
        <ul className="space-y-2">
          {articles.map((article, index) => (
            <li key={index} className="text-indigo-600 hover:text-indigo-800">
              <a href={article.link} target="_blank" rel="noopener noreferrer" className="block py-2 px-3 rounded hover:bg-indigo-50 transition-colors duration-200">
                {index + 1}. {article.title}
              </a>
            </li>
          ))}
        </ul>
      </div>
      <div
        ref={resizerRef}
        className="absolute top-0 left-0 w-2 h-full cursor-ew-resize"
        onMouseDown={handleMouseDown}
      />
    </div>
  );
};

const TopicNode = ({ name, level, volume, children, searchTerm, searchLevel, parentExpanded = true, articleCount, articles, onSelect, breadcrumb }) => {
  const [isExpanded, setIsExpanded] = useState(level === 0);
  const [isVisible, setIsVisible] = useState(true);

  useEffect(() => {
    const matchBeginningOfWord = (text, term) => {
      if (!term) return true;
      const regex = new RegExp(`\\b${term}`, 'i');
      return regex.test(text);
    };

    if (level === 0) {
      setIsVisible(true);
      setIsExpanded(true);
    } else if (searchLevel.includes(level) || searchLevel.includes('all')) {
      const shouldBeVisible = matchBeginningOfWord(name.toLowerCase(), searchTerm.toLowerCase());
      setIsVisible(shouldBeVisible);
      if (searchTerm !== '' && shouldBeVisible) {
        setIsExpanded(true);
      }
    } else {
      setIsVisible(parentExpanded);
    }
  }, [searchTerm, name, level, parentExpanded, searchLevel]);

  const toggleExpand = (e) => {
    e.stopPropagation();
    setIsExpanded(!isExpanded);
  };

  const getLevelStyle = (level) => {
    const styles = [
      'text-xl font-bold text-indigo-700',
      'text-lg font-semibold text-indigo-600',
      'text-md font-medium text-indigo-500',
      'text-sm font-normal text-indigo-400'
    ];
    return styles[level] || styles[styles.length - 1];
  };

  const getLevelBadgeColor = (level) => {
    const colors = ['bg-indigo-100 text-indigo-600', 'bg-purple-100 text-purple-600', 'bg-pink-100 text-pink-600', 'bg-red-100 text-red-600'];
    return colors[level] || colors[colors.length - 1];
  };

  const sortedChildren = useMemo(() => {
    if (!children) return [];
    if (level === 0) {
      // Sort L1 by volume
      return [...children].sort((a, b) => (b.volume || 0) - (a.volume || 0));
    } else if (level === 1 || level === 2) {
      // Sort L2 and L3 by articleCount
      return [...children].sort((a, b) => (b.articleCount || 0) - (a.articleCount || 0));
    }
    return children;
  }, [children, level]);

  const handleArticleClick = (e) => {
    e.stopPropagation();
    if (level === 3) {
      onSelect(articles, name.replace(/^\d+\s-\s/, ''), [...breadcrumb, name.replace(/^\d+\s-\s/, '')]);
    }
  };

  if (!isVisible) {
    return null;
  }

  return (
    <div className={`mb-2 ${level === 0 ? 'bg-gradient-to-r from-indigo-50 to-purple-50 rounded-lg overflow-hidden' : ''}`}>
      <div
        className={`flex items-center cursor-pointer py-4 px-6 hover:bg-indigo-50 transition-all duration-300 ${getLevelStyle(level)}`}
        onClick={toggleExpand}
        style={{ paddingLeft: level === 0 ? '16px' : `${level * 24}px` }}
      >
        <div className="flex items-center flex-grow">
          {children && (
            <span className={`${level === 0 ? 'ml-2 mr-4' : 'mr-3'} transition-transform duration-300 ${isExpanded ? 'transform rotate-90' : ''}`}>
              <ChevronRight size={20} />
            </span>
          )}
          <span className={`${getLevelBadgeColor(level)} text-xs font-medium rounded-full w-6 h-6 flex items-center justify-center mr-3`}>
            {level}
          </span>
          <span>{name.replace(/^\d+\s-\s/, '')}</span>
        </div>
        <div className="flex items-center space-x-4">
          {level === 1 && volume && (
            <Tooltip text="Monthly search volume">
              <div className="flex items-center space-x-2">
                <BarChart2 size={16} className="text-indigo-400" />
                <span className="text-sm text-indigo-400 font-normal">
                  {volume.toLocaleString()}
                </span>
              </div>
            </Tooltip>
          )}
          {level === 2 && articleCount !== undefined && (
            <Tooltip text="Number of related articles">
              <div className="flex items-center space-x-2">
                <FileText size={16} className="text-indigo-400" />
                <span className="text-sm text-indigo-400 font-normal">
                  {articleCount}
                </span>
              </div>
            </Tooltip>
          )}
          {level === 3 && articleCount !== undefined && (
            <Tooltip text="Click to view related articles">
              <div className="flex items-center space-x-2 cursor-pointer" onClick={handleArticleClick}>
                <FileText size={16} className="text-indigo-400" />
                <span className="text-sm text-indigo-400 font-normal">
                  {articleCount}
                </span>
              </div>
            </Tooltip>
          )}
        </div>
      </div>
      {children && (
        <div className={`ml-6 ${level >= 2 ? 'border-l border-indigo-100 pl-4' : ''} ${isExpanded ? '' : 'hidden'}`}>
          {sortedChildren.map((child, index) => (
            <TopicNode key={index} {...child} searchTerm={searchTerm} searchLevel={searchLevel} parentExpanded={isExpanded} onSelect={onSelect} breadcrumb={[...breadcrumb, name.replace(/^\d+\s-\s/, '')]} />
          ))}
        </div>
      )}
    </div>
  );
};

